.circle {
    position: relative;
    width: 75px;
    height: 75px;
    padding: 5% 0;
    margin: lem auto;
    margin-top: 30px;
    border-radius: 50%;
    background: #383e41;
    background-size: cover;
    user-select: none;
}

.variable {
    min-width: 60px;
    height: 60px;
    font-size: 40px;
}

.slider {
    margin-top: 10px;
    min-width: 30px;
}

td {
    height: 100px;
    min-width: 30px;
}

.num {
    border: 1px solid gray;
    padding: 6px;
    width: 60px;
    color: rgb(182, 182, 182);
    border-radius: 6px;
    font-size: 36px;
    text-align: center;
    vertical-align: middle;
    line-height: 60px;
}

.large_num {
    border: 1px solid gray;
    padding: 6px;
    width: 120px;
    color: rgb(182, 182, 182);
    border-radius: 6px;
    font-size: 36px;
    text-align: center;
    vertical-align: middle;
    line-height: 60px;
}
#result {
    width: 250px;
    color: rgb(182, 182, 182);

    border: 1px solid rgb(0, 233, 50);
    border-radius: 8px;

    text-align: center;
    vertical-align: middle;
    line-height: 60px;

    font-weight: bold;
    font-size: 36px;

    background-color: rgba(0, 0, 0, 0.603);
}
